@use 'base/variables' as v;

.alert{
  padding: calc(v.$separation / 4);
  color: v.$white;
  width: 60%;
  display: block;
  margin: 0 auto 1rem auto;
  text-align: center;
  font-weight: v.$bold;

  &.error{
    background-color: v.$red;
  }

  &.success{
    background-color: v.$green;
  }
}